// /*
// Oooh, ooh - I might have figured out a 'thing', someone tell me if I'm mad or not and what you think of this API ... (this is todo with customElements + Redux, I'll make a thread)

// It's common to register the elements when they are defined. I use Typescript and I like using the `@customElement` decorator for this. But there is one problem with it - you have to define mixins before the element is registered, so they are part of it.

// If you use Redux, there's the issue of not _really_ wanting elements to know about it (it's the wrong dependency direction - I'd rather have the app know about elements, not the other way round). But that means having to define the custom element class in one file and then register it in another after it has been wrapped with a connect mixin, and well, it's all a bit of a ball ache.

// But, I think I figured out how to connect elements to redux _after_ they might have been defined (or they can be defined first, it becomes independent).

// So now the elements can remain pure and continue registering themselves, and I can have a separate place to reference both the redux store and the elements and wire them up, mapping the state to properties and events to actions. It ends up looking like this ... or it could, does this make sense?

// ```
// import { connect } from './store'
// import { CounterElement } from './ui'

// connect(CounterElement)
//   // map state from the store to the element properties
//   .mapState((el, state) => {
//     el.count = state.counter
//   })
//   // map events from the element to store actions
//   .mapEvents({
//     'inc': e => CounterAction.increment(e.detail),
//     'dec': e => CounterAction.decrement(e.detail),
//   })
// ```

// The mapState and mapEvents are chained and each is optional (but without one at least, it does nothing)

// Incidentally, I think the event mapping could be simplified a bit by having a `dispatch` method in LitElement to remove a lot of repetitive boilerplate code when dispatching events, i.e.

// ```
// let myEvent = new CustomEvent('my-event', { 
//   detail: { message: 'my-event happened.' },
//   bubbles: true, 
//   composed: true });
// this.dispatchEvent(myEvent);
// ```
// Could be shorted to:
// ```
// this.dispatch('my-event', { message: 'my-event happened.' })
// ```

// (default to bubbles & composed, but optional 3rd param could allow overriding)

// ... and the event mapping part of connect could then just be passed the _detail_ part of the event, which is typically all the event => redux mapping uses

// Thoughts?
// */


